import { statCalculate } from "./statCalculate";

export const rank = (battle, enqueue, rankPokemon, rankType, rankValue) => {
  let pokemon;
  if (rankPokemon === "npc") {
    pokemon = battle.npc;
  } else if (rankPokemon === "player") {
    pokemon = battle.player;
  }
  let rank = pokemon.tempStatus.rank;
  let rankText = pokemon.name + " " + getStatName(rankType); //''     !
  let rankText2 = pokemon.name + " " + getStatName2(rankType); //''  !

  if (rank[rankType] === -6 && rankValue < 0) {
    rankText2 = rankText2 + "     !";
    enqueue({ battle, text: rankText2 });
    return;
  } else if (rank[rankType] === 6 && rankValue > 0) {
    rankText2 = rankText2 + "     !";
    enqueue({ battle, text: rankText2 });
    return;
  }

  rank[rankType] += rankValue;
  if (rank[rankType] > 6) {
    rank[rankType] = 6;
  }
  if (rank[rankType] < -6) {
    rank[rankType] = -6;
  }

  statCalculate(battle);

  if (rankValue > 2 || rankValue < -2) {
    rankText = rankText + " ";
  }
  if (rankValue === 2 || rankValue === -2) {
    rankText = rankText + " ";
  }
  if (rankValue > 0) {
    rankText = rankText + " !";
  }
  if (rankValue < 0) {
    rankText = rankText + " !";
  }
  enqueue({ battle, text: rankText });
};

const getStatName = (stat) => {
  switch (stat) {
    case "atk":
      return "";
    case "def":
      return "";
    case "catk":
      return " ";
    case "cdef":
      return " ";
    case "speed":
      return "";
    default:
      return "  "; //      
  }
};

const getStatName2 = (stat) => {
  switch (stat) {
    case "atk":
      return "";
    case "def":
      return "";
    case "catk":
      return " ";
    case "cdef":
      return " ";
    case "speed":
      return "";
    default:
      return "  "; //      
  }
};
